import { Phone } from "./phone-model";
var phone = new Phone();

export default {
  data() {
    return {
      phones: []
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    this.getPhone();
  },
  methods: {
    getPhone: function () {
      phone.getAll(res => {
        this.setData({
          phones: res.data
        });
      });
    },
    callPhone: function (a) {
      var e = a.currentTarget.dataset.phone;
      wx.makePhoneCall({
        phoneNumber: e
      });
    }
  }
};
